<template>
  <div class="asideR">
    <el-tabs v-model="activeName2" class="asideRTabs" @tab-click="handleClick">
      <el-tab-pane label="消息信息" name="a">
        <el-form label-position="left" label-width="80px" :model="Message">
          <el-form-item label="发送方:">
            <el-input v-model="Message.Sender" readonly />
          </el-form-item>
          <el-form-item label="接收方:">
            <el-input v-model="Message.RecUsers" readonly />
          </el-form-item>
          <el-form-item label="标题:">
            <el-input v-model="Message.Title" readonly />
          </el-form-item>
          <template v-if="Attachment.length > 0">
          <el-form-item label="附件:">
            <div class="Attachment">
              <div
                class="Attachment-icon"
                v-for="(item, index) in Attachment"
                :key="index"
                @click="getDocLocation(item)">
                <el-tooltip effect="dark" :content="item.Attachment" placement="top-start">
                <el-row>
                  <el-col :span="14" class="hideText"><span>{{ item.Attachment }}</span></el-col>
                  <el-col :span="10" class="hideText" style="text-align: right;"><span>{{ item.FileSize }}</span></el-col>
                </el-row>
                </el-tooltip>
              </div>
            </div>
          </el-form-item>
          </template>
          <el-form-item label="内容:">
            <el-input type="textarea" v-model="Message.Content" rows="10" readonly />
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <template v-if="Process === 'true'">
      <el-tab-pane label="流程" name="b">
        <Process />
      </el-tab-pane>
      </template>
    </el-tabs>
  </div>
</template>
<script>
import UserApi from "../../api/services/project";
import UserApi2 from '../../api/services/doc'
import { mapGetters } from 'vuex'
export default {
  data() {
    return {}
  },
  computed: {
    ...mapGetters('PerMessage', ['Message', 'MessageKeyword', 'Attachment', 'Process', 'activeName', 'WorkFlowKeyword']),
    activeName2: {
      get () {
        return this.activeName
      },
      set (e) {
        this.$store.commit('PerMessage/CHANGE_ACTIVENAME', e)
      }
    }
  },
  methods: {
    async getDocLocation (e) {
      // 获取 ProjectKeyWord
      let Keyword = e.AttaKeyword, IgnoreShortCut = 'false'
      const res = await UserApi.getProjectPath(Keyword, IgnoreShortCut)
      const ProjectKeyWord = res.data[0].ProjectKeyword
      //  储存位置
      await UserApi.saveLastProject(ProjectKeyWord)
      this.$router.push({ name: 'DocManagement' })
      //  获取列表
      const filterJson = '', page = 1, limit = 50
      const res3 = await UserApi2.getDocList(ProjectKeyWord, filterJson, page, limit)
      if (res3.success) {
        let KeywordList = res3.data.filter(item => item.Keyword === Keyword)
        this.$store.commit('PerMessage/ATTACHMENT_MOVE_DOC', KeywordList[0]) 
      }
    },
    handleClick(tab) {
      if (tab.name === 'b') {
        this.$store.dispatch('workflow/getWorkFlow', this.WorkFlowKeyword)
      } 
    }
  }
}
</script>
