<template>
  <el-dialog
    v-if="isFirst"
    class="dialogTemplate"
    :title="dialogObj.dialogTitle"
    :visible.sync="dialogObj.switch"
    :before-close="dialogClose">
    <el-scrollbar>
      <el-tree
        :load="loadNode"
        ref="TemplateList"
        lazy
        :props="props"
        @node-click="handleNodeClick"
        highlight-current
        node-key="text"
        :default-expanded-keys="DefaultNode"/>
    </el-scrollbar>
    <span slot="footer">
      <el-button size="small" @click="dialogClose">取 消</el-button>
      <el-button type="primary" size="small" @click="TemplateHandle">确 定</el-button>
    </span>
  </el-dialog>
</template>
<script>
import UserApi from '../../api/services/project'

export default {
  props: {
    dialogObj: {
      type: Object,
      required: true,
      default: function() {
        return {
          switch: false,
          dialogTitle: '模板选择'
        }
      }
    }
  },
  data() {
    return {
      // 默认展开的节点
      DefaultNode: ['所有模板'],
      // 模板配置
      props: {
        label: 'text',
        isLeaf: 'leaf'
      },
      // 初始值
      nodeKeyword: 'Root',
      // 节点
      TreeNodeData: {},
      // 关闭时销毁 treeData
      isFirst: false
    }
  },
  watch: {
    dialogObj: {
      handler(newValue) {
        if (newValue.switch) this.isFirst = true
        else return
      },
      deep: true
    }
  },
  methods: {
    async loadNode(node, resolve) {
      if (node.level === 0) {
        return resolve([{ text: '所有模板' }])
      }
      if (node.data.fullpath !== undefined) {
        this.nodeKeyword = node.data.fullpath
      }
      const res = await UserApi.getTempDefList(this.nodeKeyword)
      const rdata = []
      res.data.forEach(item => {
        rdata.push({
          text: item.text,
          leaf: item.leaf,
          fullpath: item.id,
          type: item.type,
          iconCls: item.iconCls
        })
      })
      resolve(rdata)
    },
    // tree 点击节点
    handleNodeClick(data) {
      console.log(data);
      this.TreeNodeData = data
    },
    // 確認
    TemplateHandle() {
      let vm = this
      if (vm.TreeNodeData.type !== vm.dialogObj.type)
        return vm.$message({ message: '请选择对应模板', type: 'error' })
      let str = vm.TreeNodeData.text,
        arr = str.split('__'),
        TemplateData = {}
      TemplateData.defname = arr[0]
      TemplateData.defdesc = arr[1]
      TemplateData.defkeyid = vm.TreeNodeData.fullpath
      TemplateData.type = vm.TreeNodeData.type
      vm.$emit('reTemplate', TemplateData)
      vm.dialogClose()
    },
    // 关闭弹窗
    dialogClose() {
      this.$emit('reData', false)
      this.isFirst = false
      this.nodeKeyword = 'Root'
    }
  }
}
</script>
<style lang="scss">
.dialogTemplate {
  .el-dialog {
    width: 40%;
    .el-dialog__body {
      height: 400px;
    }
  }
}
</style>