<template>
  <div>
    <el-row class="header">
      <el-col :span="5" class="logo">
        <img src="../../assets/i3vlogo.png" alt />
      </el-col>
      <el-col :span="15">
        <el-menu
          :default-active="activeIndex"
          mode="horizontal"
          @select="handleSelect"
          text-color="#fff">
          <el-menu-item index="1" class="active">
            <i class="el-icon-folder"></i>
            <span slot="title">文档管理</span>
          </el-menu-item>
          <el-menu-item index="2">
            <i class="el-icon-bell"></i>
            <span slot="title">个人消息</span>
          </el-menu-item>
          <el-menu-item index="3">
            <i class="el-icon-monitor"></i>
            <span slot="title">流程工作台</span>
          </el-menu-item>
          <el-menu-item index="4">
            <i class="el-icon-files"></i>
            <span slot="title">个人工作台</span>
          </el-menu-item>
          <el-menu-item index="5">
            <i class="el-icon-share"></i>
            <span slot="title">逻辑目录</span>
          </el-menu-item>
          <el-menu-item index="6">
            <i class="el-icon-search"></i>
            <span slot="title">文档查询</span>
          </el-menu-item>
          <el-menu-item index="7">
            <i class="el-icon-position"></i>
            <span slot="title">导航页</span>
          </el-menu-item>
        </el-menu>
      </el-col>
      <el-col class="UserPanel" :span="4" style="text-align: right;">
        <div style="margin-right: 20px;">
          <el-avatar
            src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"
            style="vertical-align: middle;margin-right: 10px;"
          ></el-avatar>
          <el-dropdown trigger="click">
            <span class="el-dropdown-link" style="color: #fff;">
              {{ sysUserName }}
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="PersonalCenter">
                <i class="el-icon-user-solid"></i> 个人中心
              </el-dropdown-item>
              <el-dropdown-item @click.native="RecycleBinVisible = true">
                <i class="el-icon-delete"></i> 回收站
              </el-dropdown-item>
              <el-dropdown-item divided @click.native="logout">退出</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <!-- <i class="el-icon-delete" style="margin-left: 10px; cursor:pointer"></i> -->
          <!-- <el-button type="text" icon="el-icon-delete" style="margin-left: 10px;"></el-button>
          <el-button type="text" icon="el-icon-delete" style="margin-left: 10px;"></el-button>-->
        </div>
      </el-col>
    </el-row>
    <!-- 个人中心 -->
    <el-dialog
      title="个人中心"
      :visible.sync="dialogVisible"
      width="30%"
      :modal-append-to-body="false"
      :before-close="handleClose">
      <!-- 剪裁组件弹窗 -->
      <el-dialog title="剪裁组件" :visible.sync="cropperModel" width="800px" :modal="false">
        <Cropper :img-file="file" ref="vueCropper" :fixedNumber="fixedNumber" @upload="upload" />
      </el-dialog>
      <el-form
        :model="userForm"
        ref="userForm"
        :rules="rules"
        label-position="top"
        label-width="100px">
        <el-row :gutter="20">
          <el-col :span="24" style="text-align: center;">
            <div>
              <el-avatar :size="120" :src="avatarSrc" @error="errorHandler" style="border: 1px #fafafa solid;">
                <img class="img" src="../../assets/imgerror.png" />
              </el-avatar>
              <el-upload
                action="#"
                list-type="picture"
                :auto-upload="false"
                :show-file-list="false"
                :on-change="handleCrop">
                <el-button
                  type="primary"
                  icon="el-icon-camera"
                  circle
                  style="position: absolute;top: 90px;left: 55%;padding: 8px;" />
              </el-upload>
            </div>
          </el-col>
          <el-col :span="12">
            <el-form-item label="用户名">
              <el-input v-model="userForm.O_username" disabled />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="旧密码" prop="OldPassword">
              <el-input v-model="userForm.OldPassword" placeholder="请输入旧密码" show-password />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="手机号" prop="Phone">
              <el-input v-model.number="userForm.Phone" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="新密码" prop="NewPassword">
              <el-input v-model="userForm.NewPassword" placeholder="请再次输入新密码" show-password />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="电子邮箱">
              <el-input v-model="userForm.O_email" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="确认密码" prop="NewPassword2">
              <el-input v-model="userForm.NewPassword2" placeholder="请再次输入新密码" show-password />
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="handleClose">取 消</el-button>
        <el-button type="primary" size="small" @click="submitForm('userForm')">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 回收站弹窗 -->
    <el-dialog
      title="回收站"
      :visible.sync="RecycleBinVisible"
      width="50%"
      :modal-append-to-body="false"
      :before-close="handleClose">
      <el-table
        ref="recycleData"
        :data="recycleData"
        border
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center" />
        <el-table-column prop="name" label="名称" />
        <el-table-column prop="name" label="原位置" />
        <el-table-column prop="name" label="删除日期" />
        <el-table-column prop="name" label="大小" />
        <el-table-column prop="name" label="类型" />
        <el-table-column prop="name" label="删除者" />
      </el-table>
      <el-row slot="footer" class="recycle-footer">
        <el-col :span="14" style="text-align: left;">
          <el-form :inline="true" :model="formInline" class="demo-form-inline">
            <el-form-item label="类型">
              <el-select v-model="formInline.region" placeholder="全部">
                <el-option label="区域一" value="shanghai"></el-option>
                <el-option label="区域二" value="beijing"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="时间">
              <el-select v-model="formInline.region" placeholder="全部">
                <el-option label="区域一" value="shanghai"></el-option>
                <el-option label="区域二" value="beijing"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="删除者">
              <el-select v-model="formInline.region" placeholder="全部">
                <el-option label="区域一" value="shanghai"></el-option>
                <el-option label="区域二" value="beijing"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="10">
          <el-button @click="RecycleBinVisible = false">还原</el-button>
          <el-button @click="RecycleBinVisible = false">删除</el-button>
          <el-button @click="RecycleBinVisible = false">清空回收站</el-button>
          <el-button type="primary" @click="RecycleBinVisible = false">刷新</el-button>
        </el-col>
      </el-row>
    </el-dialog>
  </div>
</template>
<script>
import UserApi from '../../api/services/user'
import UserApi2 from '../../api/services/file'
import { isTelephoneValid } from '../../util/Regular'
import Cropper from '../Cropper'
import { mapGetters } from 'vuex'
import { TimeChange } from '../../util/Time'

export default {
  data() {
    return {
      sysName: '艾三维协同系统',
      sysUserName: '账号管理',
      sysUserAvatar: '',
      dialogVisible: false, // 个人中心弹窗
      // 个人中心
      userForm: {
        O_username: '',
        O_userdesc: '',
        O_email: '',
        Phone: '',
        UserType: '',
        UserFlags: '',
        OldPassword: '',
        NewPassword: '',
        NewPassword2: ''
      },
      rules: {
        Phone: [
          { required: false, message: '请输入电话', trigger: 'blur' },
          {
            validator: (rule, value, callback) => {
              if (value) {
                setTimeout(() => {
                  if (isTelephoneValid(value)) {
                    callback()
                  } else {
                    callback(new Error('手机号码格式不正确'))
                  }
                }, 250)
              } else {
                callback()
              }
            }
          }
        ],
        O_email: [
          { required: false, message: '请输入电子邮箱', trigger: 'blur' },
          { type: 'email', message: '格式不对', trigger: 'blur' }
        ],
        OldPassword: [
          { required: false, message: '请输入旧密码', trigger: 'blur' },
          { min: 5, max: 25, message: '长度在 5 到 25个字符' },
          {
            pattern: /^(\w){5,25}$/,
            message: '只能输入5-25个字母、数字、下划线'
          }
        ],
        NewPassword: [
          { required: false, message: '请输入新密码', trigger: 'blur' },
          { min: 5, max: 25, message: '长度在 5 到 25个字符' },
          {
            pattern: /^(\w){5,25}$/,
            message: '只能输入5-25个字母、数字、下划线'
          }
        ],
        NewPassword2: [
          { required: false, message: '请输入新密码', trigger: 'blur' },
          { min: 5, max: 25, message: '长度在 5 到 25个字符' },
          {
            pattern: /^(\w){5,25}$/,
            message: '只能输入5-25个字母、数字、下划线'
          },
          {
            validator: (rule, value, callback) => {
              if (this.userForm.NewPassword) {
                if (value !== this.userForm.NewPassword) {
                  callback(new Error('两次输入密码不一致!'))
                } else {
                  callback()
                }
              } else {
                callback()
              }
            }
          }
        ]
      },
      file: '', // 当前被选择的图片文件
      imageUrl: '', // 单图情况框内图片链接
      uploadList: [], // 上传图片列表
      cropperModel: false, // 剪裁组件弹窗
      fixedNumber: [1, 1],
      avatarSrc: '',
      errorHandler: true,
      ServerFullFileName: '',
      // 回收站
      RecycleBinVisible: false,
      recycleData: [
        {
          name: 'aaaa'
        },
        {
          name: 'aaaa'
        },
        {
          name: 'aaaa'
        },
        {
          name: 'aaaa'
        },
        {
          name: 'aaaa'
        },
        {
          name: 'aaaa'
        },
        {
          name: 'aaaa'
        }
      ],
      formInline: {
        user: '',
        region: ''
      }
    }
  },
  components: {
    Cropper
  },
  mounted() {
    let username =
      localStorage.getItem('username') || sessionStorage.getItem('username')
    this.sysUserName = username || this.sysUserName
  },
  updated() {
    if (this.$refs.vueCropper) {
      this.$refs.vueCropper.Update()
    }
  },
  computed: {
    ...mapGetters(['UserKeyword']),
    activeIndex () {
      return this.$store.state.headerActiveIndex
    }
  },
  methods: {
    handleClose() {
      this.dialogVisible = false
      this.cropperModel = false
      this.errorHandler = true
      this.userForm = {
        O_username: '',
        O_userdesc: '',
        O_email: '',
        Phone: '',
        UserType: '',
        UserFlags: '',
        OldPassword: '',
        NewPassword: '',
        NewPassword2: ''
      }
    },
    async editPersonalCenter() {
      let { OldPassword } = this.userForm,
        { NewPassword } = this.userForm
      if (OldPassword !== '' || NewPassword !== '') {
        const res = await UserApi.setUserPassword(OldPassword, NewPassword)
        if (!res.success) {
          this.$message({ message: res.msg, type: 'error' })
          return
        }
      }
      let { UserKeyword } = this,
        UserCode = this.userForm.O_username,
        UserDesc = this.userForm.O_userdesc,
        UserEmail = this.userForm.O_email,
        UserType = this.userForm.UserType,
        UserStatus = this.userForm.UserStatus,
        Phone = this.userForm.Phone
      const res2 = await UserApi.saveUserInfo(
        UserKeyword,
        UserCode,
        UserDesc,
        UserEmail,
        UserType,
        UserStatus,
        Phone
      )
      if (res2.success) {
        this.$message({ message: '修改成功', type: 'success' })
        this.dialogVisible = false
        this.handleClose()
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.editPersonalCenter()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 开启个人中心
    async PersonalCenter() {
      this.dialogVisible = true
      let { UserKeyword } = this
      const res = await UserApi.getUserInfo(UserKeyword)
      if (res.success) {
        this.userForm.O_username = res.data[0].O_username
        this.userForm.O_userdesc = res.data[0].O_userdesc
        this.userForm.O_email = res.data[0].O_email
        this.userForm.UserType = res.data[0].UserType
        this.userForm.UserStatus = res.data[0].UserFlags
        if (res.data[0].Phone) this.userForm.Phone = Number(res.data[0].Phone)
        let FileName = `${res.data[0].O_username}.jpg`
        this.avatarSrc = `BMP/${FileName}?timestamp=${Date.now()}`
        if (this.avatarSrc) {
          this.errorHandler = false
        }
      }
    },
    // 点击弹出剪裁框
    handleCrop(file) {
      this.cropperModel = true
      this.file = file
    },
    // 剪裁框保存
    upload(data) {
      this.avatarSrc = data
      this.beforeAvatarUpload()
    },
    async beforeAvatarUpload() {
      let { file } = this
      if (file.name) {
        let fileSize = file.size,
          ModifyDate = TimeChange(file.raw.lastModifiedDate),
          ServerFileName = `BMP/${this.userForm.O_username}.jpg`
        let ObjectKeyword = '',
          CreateDate = ''
        const res = await UserApi2.beforeUploadFile(
          ObjectKeyword,
          ServerFileName,
          CreateDate,
          ModifyDate,
          fileSize
        )
        if (res.success) {
          this.ServerFullFileName = res.data[0].ServerFullFileName
          this.httpRequest()
        }
      }
    },
    async httpRequest() {
      if (this.ServerFullFileName) {
        let { ServerFullFileName } = this,
          { file } = this
        let Files = file.raw
        const res = await UserApi2.uploadFile(ServerFullFileName, Files)
        if (res.success) this.onSuccess()
      }
    },
    // 用户表单 - 上传文件成功
    async onSuccess() {
      let DocKeyword = ''
      let { ServerFullFileName } = this
      const res = await UserApi2.afterUploadFile(
        DocKeyword,
        ServerFullFileName
      )
      if (res.success) {
        this.cropperModel = false
        this.$message({ message: '上传成功', type: 'success' })
      }
    },
    // 用户表单 - 上传文件失败
    onError() {
      this.$message.error('上传失败')
    },
    handleSelectionChange(val) {
      console.log(val)
    },
    handleSelect(key) {
      switch (key) {
        case '1': // 文档管理
          this.$router.push({ name: 'DocManagement' })
          break
        case '2': // 个人消息
          this.$router.push({ name: 'PerMessage' })
          break
        case '3': // 流程工作台
          this.$router.push({ name: 'ProWorkbench' })
          break
        case '4': // 个人工作台
          this.$router.push({ name: 'PerWorkbench' })
          break
        case '5': // 逻辑目录
          this.$router.push({ name: 'LogicalDirectory' })
          break
        case '6': // 文档查询
          this.$router.push({ name: 'DocSelect' })
          break
        case '7': // 导航页
          this.$router.push({ name: 'Navigation' })
          break
      }
    },
    //退出登录
    logout: function() {
      var vm = this
      vm.$confirm('确认退出吗?', '提示', {
        //type: 'warning'
      })
        .then(() => {
          vm.$store.commit('REMOVE_USER')
          vm.$router.push('/login')
        })
        .catch(() => {})
    }
  }
}
</script>
<style lang="scss">
.UserPanel {
  .el-dropdown-link {
    cursor: pointer;
    color: #fff;
  }
  .el-button--text:focus,
  .el-button--text:hover {
    color: #ffbc40;
  }
}
</style>
<style lang="scss">
@import "@/scss/_variables";
.header {
  height: 60px;
  line-height: 4rem;
  background: $main-color-light;
  color: #fff;
  .el-menu {
    background-color: $main-color-light;
    .el-menu-item i {
      color: #fff;
    }
    .el-menu-item.is-active {
      border-bottom: 0;
      color: #fff;
      background-color: #2b76fd;
    }
  }
  .el-menu--horizontal {
    border-bottom: 0;
    .el-menu-item {
      border-bottom: 0;
    }
    .el-menu-item:not(.is-disabled):hover,
    .el-menu-item:not(.is-disabled):focus {
      background-color: #2b76fd;
      color: #fff;
    }
  }
  .el-menu.el-menu--horizontal {
    border-bottom: 0;
  }
  .logo {
    font-size: 22px;
    padding: 0 20px;
    // text-align: center;
    padding: 13px 20px 9px;
    color: #fff;
    line-height: 2rem;
  }
}
.recycle-footer {
  .el-form-item__content {
    width: 100px;
  }
}
@media screen and (max-width: 1400px) {
  .el-menu {
    .el-menu-item {
      padding: 0 15px;
    }
  }
}
@media screen and (max-width: 1300px) {
  .el-menu {
    .el-menu-item {
      padding: 0 5px;
    }
  }
}
</style>
