<template>
  <div class="asideL">
    <el-row>
      <el-col :span="24">
        <el-button
          size="small"
          type="primary"
          icon="el-icon-plus"
          @click="CreateNewRootProject"
        >新建根目录</el-button>
        <el-button size="small" type="primary" icon="el-icon-user" @click="openUser">用户</el-button>
        <el-button size="small" type="primary" icon="el-icon-user-solid" @click="openUserGroup">用户组</el-button>
      </el-col>
    </el-row>
    <div class="asideLLine" />
    <el-scrollbar v-loading="loading">
      <div class="asideLTreeOuter" @contextmenu.prevent="contextMenuClick">
        <el-tree
          v-if="isReset"
          :props="props"
          :load="loadNode"
          class="asideLTree"
          lazy
          highlight-current
          ref="DocList"
          @node-click="handleNodeClick"
          node-key="projectId"
          @node-contextmenu="contextMenuClick"
          :default-expanded-keys="beforeNode"
        ></el-tree>
      </div>
    </el-scrollbar>
    <!-- 右键功能 -->
    <div v-if="menuVisible" class="rightMenu" ref="rightMenu" :style="styleObject">
      <ul class="rightMenu-ul">
        <li
          v-for="(item, index) in MenuList"
          :key="index"
          class="rightMenu-li"
          :class="item.State === 'Enabled'? '': 'RM-disabled'"
          @click="getMenuRight(item)"
        >{{ item.Name }}</li>
      </ul>
    </div>
    <User :dialogObj="userData" @reData="reUserData" />
    <UserGroup :dialogObj="userGroupData" @reData="reGroupData" />
    <TreeContextMenu :dialogObj="rightData" @reData="reData(arguments)" />
    <!-- 二级弹窗 - 增加用户/用户组 -->
    <PermissionList :dialogObj="PermissionData" @reData="rePData" @reUser="reUser(arguments)" />
  </div>
</template>
<script>
import UserApi from "../../api/services/project";
import UserApi2 from "../../api/services/user";
import UserApi3 from "../../api/services/doc";
import UserApi4 from "../../api/services/workflow";
import UserApi5 from "../../api/services/dbsource";
import PermissionList from "../Dialog/PermissionList-PM2";
import { mapGetters } from "vuex";

export default {
  data() {
    return {
      rightData: {
        switch: false,
        editForm: {}
      },
      userData: {
        switch: false
      },
      userGroupData: {
        switch: false
      },
      collapsed: false,
      // 左侧选单 - 配置选项
      props: {
        label: "text",
        isLeaf: "leaf"
      },
      // 左侧选单 - 右键
      menuVisible: false,
      styleObject: {
        top: 0,
        left: 0,
        opacity: 0
      },
      projectId: "",
      NewUserGroupChildren: {},
      node: {},
      resolve: {},
      isFirst: true,
      contextMenuObj: {},
      // 上次節點位置
      beforeNode: [],
      // 返回流程
      reProcessData: {},
      // 二级弹窗 - 增加用户/用户组
      PermissionData: {
        switch: false
      },
      MenuList: [],
      len: 0,
      isReset: true,
      loading: true
    };
  },
  components: {
    PermissionList
  },
  mounted() {
    this.getProjectPath();
  },
  computed: {
    ...mapGetters("project", ["isRefreshTree"])
  },
  watch: {
    // 个人消息附件跳转过来
    isRefreshTree: {
      handler(newValue) {
        if (newValue) {
          this.isReset = false;
          this.loading = true;
          this.getProjectPath();
          this.$store.commit("project/REFRESH_TREE", false);
        }
        this.$nextTick(() => {
          this.isReset = true;
          this.loading = false;
        });
      }
    }
  },
  methods: {
    // 開啟添加用戶彈窗
    openPermissionList() {
      this.PermissionData.switch = true;
    },
    // 添加用戶彈窗 返回
    rePData(e) {
      this.PermissionData.switch = e;
    },
    // 添加用戶彈窗 返回
    async reUser(e) {
      let vm = this;
      if (e[2] !== undefined) {
        let DocList = vm.projectId,
          WfKeyword = vm.reProcessData.o_code,
          userlist = e[2];
        const res = await UserApi4.startNewWorkFlow(
          DocList,
          WfKeyword,
          userlist
        );
        if (res.success) {
          this.$message({ message: "添加流程成功", type: "success" });
        }
      }
    },
    // 上次开启位置
    async getProjectPath() {
      let Keyword = "LastProject",
        IgnoreShortCut = "false";
      const res = await UserApi.getProjectPath(Keyword, IgnoreShortCut);
      if (res.success) {
        let arrStr = res.data[0].NodeId.split("_");
        let data = {};
        for (let i = 0; i < arrStr.length; i++) {
          data.projectId = arrStr[i];
          this.beforeNode.push(arrStr[i]);
        }
        this.handleNodeClick(data);
      }
    },
    // 新建根目录
    async CreateNewRootProject() {
      let vm = this,
        Menu = "CreateNewRootProject",
        action = "CreateProject";
      const res = await UserApi.getMenuRight(vm.projectId, Menu);
      if (res.success) {
        const res2 = await UserApi.getProjectAttr(action, vm.projectId);
        vm.rightData.options = res2.data[0].storagelist;
        this.NewUserGroupChildren = undefined;
      } else {
        this.$message({ message: res.msg, type: "error" });
        return;
      }
      vm.rightData.switch = true;
      vm.rightData.Id = "MS_CreateNewRootProject";
      vm.rightData.dialogTitle = "新建根目录";
      vm.rightData.projectId = this.projectId;
    },
    // 右键选单选取
    async getMenuRight(e) {
      let vm = this,
        Menu = "",
        action = "CreateProject";
      if (e.Id === "MS_CreateNewProject") {
        Menu = "CreateNewProject";
      } else if (e.Id === "MS_CreateNewRootProject") {
        Menu = "CreateNewRootProject";
      }
      if (
        e.Id === "MS_CreateNewProject" ||
        e.Id === "MS_CreateNewRootProject" ||
        e.Id === "MS_CreateNewRootProjec_2"
      ) {
        const res = await UserApi.getMenuRight(vm.projectId, Menu);
        if (res.success) {
          const res2 = await UserApi.getProjectAttr(action, vm.projectId);
          vm.rightData.options = res2.data[0].storagelist;
          if (
            e.Id === "MS_CreateNewRootProject" ||
            e.Id === "MS_CreateNewRootProjec_2"
          ) {
            vm.NewUserGroupChildren = undefined;
          }
        } else {
          vm.$message({ message: res.msg, type: "error" });
          return;
        }
      } else if (e.Id === "MS_ModiProjAttr") {
        (Menu = "ModiProjAttr"), (action = "ModiProject");
        const res = await UserApi.getMenuRight(vm.projectId, Menu);
        if (res.success) {
          const res2 = await UserApi.getProjectAttr(action, vm.projectId);
          vm.rightData.options = res2.data[0].storagelist;
          vm.rightData.editForm.projname = res2.data[0].projname;
          vm.rightData.editForm.projdesc = res2.data[0].projdesc;
          vm.rightData.editForm.defname = res2.data[0].defname;
          vm.rightData.editForm.defdesc = res2.data[0].defdesc;
          vm.rightData.isHide = res2.data[0].isHide;
        } else {
          vm.$message({ message: res.msg, type: "error" });
          return;
        }
      } else if (e.Id === "MS_AddFavorites") {
        const res = await UserApi2.addFavorites(vm.projectId);
        if (res.data[0].state === "addSuccess") {
          vm.$message({ message: "已添加到收藏夹", type: "success" });
        }
      } else if (e.Id === "MS_CreateNewDoc") {
        (Menu = "CreateNewDoc"), (action = "CreateDoc");
        const res = await UserApi.getMenuRight(vm.projectId, Menu);
        if (res.success) {
          const res2 = await UserApi3.getDocAttr(action, vm.projectId);
        } else {
          vm.$message({ message: res.msg, type: "error" });
          return;
        }
      } else if (e.Id === "MS_BatchCreateDoc") {
        Menu = "CreateNewDoc";
        const res = await UserApi.getMenuRight(vm.projectId, Menu);
        if (!res.success) {
          vm.$message({ message: res.msg, type: "error" });
          return;
        }
      } else if (e.Id === "MS_CreateProjByDef") {
        Menu = "CreateNewProject";
        const res = await UserApi.getMenuRight(vm.projectId, Menu);
        vm.rightData.contextMenuObj = vm.contextMenuObj;
        if (!res.success) {
          vm.$message({ message: res.msg, type: "error" });
          return;
        }
      }
      // MS_DeleteProject, MS_CreateGlobSearch, MS_StartProjectFlow 不用 getMenuRight
      vm.rightData.switch = true;
      vm.rightData.Id = e.Id;
      vm.rightData.dialogTitle = e.Name;
      vm.rightData.projectId = vm.projectId;
    },
    reData(e) {
      let vm = this;
      vm.rightData.switch = e[0];
      if (e[1] === "del") {
        return vm.$refs.DocList.remove(vm.projectId);
      } else if (e[1] === "add") {
        let data = {};
        data.projectId = e[2].fullpath;
        data.fullpath = e[2].fullpath;
        data.text = e[2].text;
        data.leaf = false;
        if (vm.NewUserGroupChildren === undefined) {
          // vm.node.childNodes = [] // 这里把子节点清空，是因为再次调用 loadNode 的时候会往 childNodes 里 push 节点，所以会有节点重复的情况。
          // vm.loadNode(vm.node, vm.resolve)
          vm.node.loaded = false;
          vm.node.expand();
        } else {
          data.fullpath = vm.NewUserGroupChildren.data.projectId;
          vm.$set(vm.NewUserGroupChildren, "isLeafByUser", false);
          vm.$refs.DocList.append(data, vm.projectId);
        }
      } else if (e[1] === "edit") {
        vm.$set(vm.contextMenuObj, "text", e[2]);
      } else if (e[1] === "start") {
        this.reProcessData = e[2];
        this.openPermissionList();
      }
      vm.contextMenuObj = {};
    },
    // 用户
    openUser() {
      this.userData.switch = true;
    },
    reUserData(e) {
      this.userData.switch = e;
    },
    // 用户组
    openUserGroup() {
      this.userGroupData.switch = true;
    },
    reGroupData(e) {
      this.userGroupData.switch = e;
    },
    showMenu(i, status) {
      this.$refs.menuCollapsed.getElementsByClassName(
        "submenu-hook-" + i
      )[0].style.display = status ? "block" : "none";
    },
    handleAvatarSuccess(res, file) {
      console.log(file.name);
      console.log(URL.createObjectURL(file.raw));
    },
    // 左侧选单 - 加载子树数据
    async loadNode(node, resolve) {
      if (this.isFirst) {
        this.node = node;
        this.resolve = resolve;
        this.isFirst = false;
      }
      let nodeKeyword = "Root";
      if (node.data !== undefined) {
        nodeKeyword = node.data.projectId;
      }
      const ProjectType = "1";
      const res = await UserApi.getProjectList(nodeKeyword, ProjectType);
      const rdata = [];
      let ProjectList = res.data.map(item => item.Keyword).join(",");
      const res2 = await UserApi.getChildsDocsCount(ProjectList);
      let nodeFilesTotal = res2.data[0];
      res.data.forEach(item => {
        let str = "";
        if (nodeFilesTotal[item.Keyword] > 0) {
          str = `${item.text}【${nodeFilesTotal[item.Keyword]}】`;
        } else {
          str = item.text;
        }
        rdata.push({
          text: str,
          leaf: item.leaf,
          projectId: item.Keyword,
          fullpath: item.id
        });
      });
      resolve(rdata);
      this.loading = false;
    },
    // 左侧选单 - 节点被点击时的回调
    async handleNodeClick(data) {
      // let str = data.fullpath.split('_').pop()
      var vm = this;
      vm.menuVisible = false;
      const ProjectKeyWord = data.projectId,
        filterJson = "",
        page = 1,
        limit = 50;
      vm.projectId = data.projectId;
      if (data.fullpath) {
        const res = await UserApi.saveLastProject(ProjectKeyWord);
      }
      vm.$store.dispatch("doc/getDocList", {
        ProjectKeyWord,
        filterJson,
        page,
        limit
      });
      vm.$store.dispatch("doc/getProjectShowPath", ProjectKeyWord);
      vm.$store.dispatch("doc/getProjectBaseAttr", ProjectKeyWord);
    },
    // 左侧选单 - 右键
    contextMenuClick(MouseEvent, object, Node, element) {
      let vm = this;
      vm.NewUserGroupChildren = Node;
      vm.menuVisible = false;
      vm.menuVisible = true;
      let ProjectList = "",
        Position = "TVProject",
        TvPosition = "1";
      if (object === undefined) {
        vm.projectId = "";
        Position = "TVContainer";
      } else {
        vm.projectId = object.projectId;
        ProjectList = object.projectId;
        vm.contextMenuObj = object;
      }
      // vm.$store.dispatch('dbsource/getMenuList', { ProjectList, Position, TvPosition })
      UserApi5.getMenuList(ProjectList, Position, TvPosition).then(res => {
        vm.MenuList = res.data;
        vm.len = res.data.length * 33;
        vm.styleObject.opacity = 1;
        if (res.data.length === 1) {
          vm.styleObject.top = MouseEvent.clientY - 80 + "px";
          vm.styleObject.left = MouseEvent.clientX + 30 + "px";
        } else {
          // getBoundingClientRect 獲取視窗寬高
          if (
            MouseEvent.clientY >
            vm.$el.getBoundingClientRect().height / 1.1
          ) {
            vm.styleObject.top = MouseEvent.clientY - vm.len - 100 + "px";
          } else if (
            MouseEvent.clientY >
            vm.$el.getBoundingClientRect().height / 2
          ) {
            vm.styleObject.top = MouseEvent.clientY - vm.len + 80 + "px";
          } else {
            vm.styleObject.top = MouseEvent.clientY - 80 + "px";
          }
          vm.styleObject.left = MouseEvent.clientX + 30 + "px";
        }
      });
      // if (MouseEvent.clientX > vm.$el.getBoundingClientRect().width / 1.8) {
      //   console.log(1111111111);
      //   vm.styleObject.left = MouseEvent.clientX - vm.$refs.rightMenu.clientWidth - 50 + 'px'
      // } else {
      // vm.styleObject.left = MouseEvent.clientX + 40 + 'px'
      // }
      const ProjectKeyWord = object.projectId,
        filterJson = "",
        page = 1,
        limit = 50;
      vm.$store.dispatch("doc/getDocList", {
        ProjectKeyWord,
        filterJson,
        page,
        limit
      });
      vm.$store.dispatch("doc/getProjectShowPath", ProjectKeyWord);
      vm.$store.dispatch("doc/getProjectBaseAttr", ProjectKeyWord);
      document.addEventListener("click", vm.foo);
      // document.addEventListener('contextmenu', vm.foo)
    },
    // 左侧选单 - 取消右键
    foo() {
      // 取消鼠标监听事件
      let vm = this;
      vm.menuVisible = false;
      vm.styleObject.opacity = 0;
      vm.styleObject.top = 0;
      vm.styleObject.left = 0;
      document.removeEventListener("mousedown", vm.foo);
    }
  }
};
</script>
<style lang="scss">
.filter-tree {
  height: 250px;
  overflow-y: scroll;
}
</style>
<style lang="scss">
.rightMenu {
  position: absolute;
  top: 163px;
  left: 37px;
  z-index: 2131;
  display: block;
  opacity: 1;
  box-shadow: rgba(0, 0, 0, 0.2) 2px 2px 7px 2px;
  background-color: #f1f1f1;
  border: 1px solid #ddd;
  border-radius: 0.3rem;
  .rightMenu-ul {
    position: relative;
    z-index: 100;
    min-width: 150px;
    border: none;
    padding: 5px 0 10px;
    margin: 0;
    list-style: none;
  }
  .rightMenu-li {
    height: 32px;
    border-bottom: 1px dotted #ccc;
    line-height: 34px;
    font-size: 14px;
    color: #717171;
    margin: 0 10px;
    padding: 0 10px;
    list-style: none;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .rightMenu-li:last-child {
    border-bottom: 0;
  }
  .rightMenu-li:hover {
    background: #528ffd;
    color: #fff;
  }
  .RM-disabled {
    pointer-events: none;
    color: rgb(187, 187, 187);
  }
}
.el-dialog__body {
  border-bottom: 1px solid #dddddd;
  border-top: 1px solid #dddddd;
  padding: 25px 20px;
  margin: 5px 0;
}
</style>

