<template>
  <div class="asideL">
    <h3>
      <i class="el-icon-s-order" style="color: #FFBC40;"></i> 流程分类
    </h3>
    <div class="asideLLine" />
    <el-scrollbar v-loading="loading">
      <div class="asideLTreeOuter">
        <el-tree
          ref="treeBox"
          :props="props"
          :load="loadNode"
          node-key="id"
          class="asideLTree"
          lazy
          highlight-current
          @node-click="handleNodeClick"
        />
      </div>
    </el-scrollbar>
  </div>
</template>
<script>
import UserApi from "../../api/services/workflow";
export default {
  data() {
    return {
      // 左侧选单 - 配置选项
      props: {
        label: "name",
        children: "zones",
        isLeaf: "leaf"
      },
      loading: true
    };
  },
  methods: {
    // 左侧选单 - 加载子树数据
    async loadNode(node, resolve) {
      const nodeKeyword = "";
      const res = await UserApi.getWorkFlowPlaceTree(nodeKeyword);
      const rdata = [];
      res.data.forEach(item => {
        rdata.push({
          id: item.id,
          name: item.text,
          leaf: item.leaf
        });
      });
      resolve(rdata);
      this.loading = false;
      this.$nextTick(() => {
        this.$refs.treeBox.setCurrentKey(rdata[0].id);
        const data = {};
        data.id = rdata[0].id;
        this.handleNodeClick(data);
      });
    },
    // 左侧选单 - 节点被点击时的回调
    handleNodeClick(data) {
      const WorkflowType = data.id,
        page = 1;
      this.$store.dispatch("workflow/getWorkFlowPlaceList", {
        WorkflowType,
        page
      });
    }
  }
};
</script>

