<template>
  <div id="home">
    <el-container>
      <el-header><Header /></el-header>
      <el-container>
        <transition @enter="enter" @leave="leave" :css="false">
          <el-aside id="left" v-show="leftToggle" :width="asideLwidth">
            <el-row style="height: 100%;">
              <el-col v-show="leftToggle" :span="22" style="height: 100%;"><AsideL /></el-col>
              <el-col :span="1" style="height: 100%;">
                <i class="iconBtn" :class="leftToggle? 'el-icon-caret-left' : 'el-icon-caret-right'" @click="LeftMenuSwitch" />
              </el-col>
              <el-col v-show="leftToggle" :span="1" style="height: 100%;">
                <i id="resize" class="el-icon-caret-right" />
              </el-col>
            </el-row>
          </el-aside>
        </transition>
        <el-main id="main" :width="Mainwidth"><Main /></el-main>
        <transition @enter="enter2" @leave="leave2" :css="false">
          <el-aside id="right" v-show="rightToggle" :width="asideRwidth">
            <el-row class="right_position" style="height: 100%;">
              <el-col v-show="rightToggle" :span="1" style="height: 100%;">
                <i id="resize2" class="el-icon-caret-left" />
              </el-col>
              <el-col v-show="rightToggle" :span="22" style="height: 100%;"><AsideR /></el-col>
              <el-col :span="1" style="height: 100%;">
                <i class="iconBtn" :class="rightToggle? 'el-icon-caret-right' : 'el-icon-caret-left'" @click="RightMenuSwitch" />
              </el-col>
            </el-row>
          </el-aside>
        </transition>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import Header from '@/components/Home/Header.vue'
import AsideL from '@/components/Home/AsideL'
import Main from '@/components/Home/Main.vue'
import AsideR from '@/components/Home/AsideR.vue'
import { Drag } from '@/util/Drag'
import { TweenMax, Power4 } from 'gsap'
import { mapGetters } from 'vuex'

export default {
  name: 'home',
  data() {
    return {
      asideLwidth: '28rem',
      Mainwidth: '42rem',
      leftToggle: true,
      rightToggle: true,
    }
  },
  mounted() {
    let resize = document.getElementById('resize')
    let resize2 = document.getElementById('resize2')
    let left = document.getElementById('left')
    let main = document.getElementById('main')
    let right = document.getElementById('right')
    let home = document.getElementById('home')
    Drag(resize, resize2, left, main, right, home)
  },
  computed: {
    ...mapGetters('asd', ['qwe']),
    asideRwidth () {
      let aa = '28rem'
      if (this.qwe === 'e' || this.qwe === 'g') {
        aa = '80rem'
      } else {
        aa = '28rem'
      }
      return aa
    }
  },
  components: {
    Header,
    AsideL,
    Main,
    AsideR
  },
  methods: {
    LeftMenuSwitch() {
      this.leftToggle = !this.leftToggle
    },
    RightMenuSwitch() {
      this.rightToggle = !this.rightToggle
    },
    enter: function(el, done) {
      TweenMax.to(el, 0.6, {
        width: 20 + '%',
        paddingLeft: 15 + 'px',
        ease: Power4.easeOut
      })
    },
    leave: function(el, done) {
      TweenMax.to(el, 0.6, {
        width: 30 + 'px',
        paddingLeft: 3 + 'px',
        ease: Power4.easeOut
      })
    },
    enter2: function(el, done) {
      TweenMax.to(el, 0.6, {
        width: 20 + '%',
        paddingLeft: 0 + 'px',
        ease: Power4.easeOut
      })
    },
    leave2: function(el, done) {
      TweenMax.to(el, 0.6, {
        width: 30 + 'px',
        paddingLeft: 4 + 'px',
        ease: Power4.easeOut
      })
    }
  }
}
</script>
<style lang="scss" scoped>
#home {
  width: 100%;
  height: 100%;
  position: relative;
  overflow-y: hidden;
  .el-container {
    background-color: #f5f6fa;
    height: 100%;
    .el-header {
      position: fixed;
      width: 100%;
      left: 0;
      top: 0;
      z-index: 999;
      padding: 0;
      box-sizing: content-box;
    }
    .el-container {
      height: calc(100% - 60px);
      margin-top: 60px;
      .el-aside {
        padding: 15px;
        background-color: #fff;
      }
      .el-aside#left {
        overflow: inherit;
        padding-right: 0;
        border-right: 1px solid #ebeef5;
        box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
        .el-col-22 {
          width: calc(100% - 40px);
        }
        .el-col-1 {
          width: 20px;
          #resize {
            display: block;
            font-size: 16px;
            width: 10px;
            cursor: w-resize;
            padding-top: 380px;
          }
        }
      }
      .el-main {
        background-color: #fff;
        margin: 15px;
        border-right: 1px solid #ebeef5;
        box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
      }
      .el-aside#right {
        padding-left: 0;
        border-left: 1px solid #ebeef5;
        box-shadow: -2px 0 12px 0 rgba(0, 0, 0, 0.1);
        .el-row.right_position {
          position: static;
        }
        .el-col-22 {
          width: calc(100% - 40px);
        }
        .el-col-1 {
          width: 20px;
          #resize2 {
            display: block;
            font-size: 16px;
            width: 10px;
            cursor: w-resize;
            padding-top: 380px;
          }
        }
      }
    }
  }
}
.iconBtn {
  font-size: 15px;
  padding: 4px;
  color: #ffffff;
  background: #FFBC40;
  border-radius: 50%;
  cursor: pointer;
}
.iconBtn:hover {
  background: #FFCD70;
}
</style>
<style lang="scss">
  @import "@/scss/Common";
</style>