  /**
   * 換算時間
   * @param {Number} OriginalTime Sat Jan 11 2020 21:08:19 GMT+0800 (中國標準時間)
   * @return {String} 2020-01-11T21:08:19
   */
  export function p (s) {
    return s < 10 ? '0' + s : s
  }
  export function TimeChange (OriginalTime) {
    let NewTime = OriginalTime.getFullYear() + '-' + p((OriginalTime.getMonth() + 1)) + '-' + p(OriginalTime.getDate()) + 'T' + p(OriginalTime.getHours()) + ':' + p(OriginalTime.getMinutes()) + ':' + p(OriginalTime.getSeconds())
    return NewTime
  }
