<template>
  <div>
    <el-upload
      ref="upload"
      action="#"
      multiple
      :auto-upload="false"
      :on-change="handleChange"
      :show-file-list="false"
      :on-error="onError"
    >
      <el-button id="ReplaceFile" size="small" type="primary" icon="el-icon-upload2">上传</el-button>
    </el-upload>
    <!-- 升级覆盖弹窗 -->
    <el-dialog
      title="确认文件替换"
      :visible.sync="upgradeDialogVisible"
      width="40%"
      center
      :before-close="handleClose"
      :append-to-body="true"
    >
      <span style="text-align: center;">目录下已存在同名文件是否升级或覆盖?</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirmHandle('true')">升级</el-button>
        <el-button type="primary" @click="confirmAllHandle('allUpgrade');confirmHandle('true')">全部升级</el-button>
        <el-button type="primary" @click="confirmHandle('false')">覆盖</el-button>
        <el-button type="primary" @click="confirmAllHandle('allCover');confirmHandle('false')">全部覆盖</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="上传文件"
      :visible.sync="progressVisible"
      width="40%"
      :before-close="handleClose"
      :append-to-body="true"
      class="uploadProgress"
    >
      <el-table :data="fileData">
        <el-table-column prop="fileName" label="文件名"></el-table-column>
        <el-table-column prop="size" label="大小" width="100">
          <template slot-scope="scope">
            <span>{{scope.row.size | filterSize(scope.row.size)}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="progress" label="进度" width="200">
          <template slot-scope="scope">
            <el-progress :stroke-width="20" :text-inside="true" :percentage="scope.row.progressBar"></el-progress>
          </template>
        </el-table-column>
        <!-- <el-table-column width="50">
          <template slot-scope="scope">
            <div class="delUpload" @click="cancelUpload(scope.row.uid)">×</div>
          </template>
        </el-table-column>-->
      </el-table>
      <span slot="footer">
        <div class="uploadDetails">已上传{{percent}}% (共{{fileNum}}个文件), 上传速度：{{uploadSpeed}}{{unit}}</div>
      </span>
    </el-dialog>
    <InTray :dialogObj="InTrayData" @reData="reInTray" />
  </div>
</template>
<script>
import UserApi2 from "../../api/services/file";
import UserApi3 from "../../api/services/doc";
import { TimeChange } from "../../util/Time";
import { mapGetters } from "vuex";

export default {
  props: {
    uploadArr: {
      type: Array
    },
    // 是否自动上传,默认true,用于创建发文，生成提资单来改变上传方式
    autoUpload: {
      type: Boolean
    }
  },
  data() {
    return {
      multiUploadSize: 4058936, //大于这个数值的文件使用断点续传
      eachSize: 4058936, //每块文件大小
      requestCancelUpload: [], //请求方法队列(取消上传)
      // 进度条显示详情
      percent: 0,
      fileNum: 0,
      uploadSpeed: 0,
      fileData: [],
      unit: "",
      progressArr: [],
      //收文处理弹窗
      InTrayData: {
        switch: false
      },
      progressVisible: false,
      customColor: "#409eff",
      // 文件暂存
      fileList: [],
      TimeId: -1,
      // 弹窗 - 开关
      upgradeDialogVisible: false,
      // 弹窗 - 按钮全部
      isAll: "",
      // 弹窗 - 按钮返回值
      confirmUpgrade: "",
      // 暂存有重复的文件
      DuplicateFiles: [],
      DuplicateKeywords: [],
      ServerFullFileNames: [],
      i: 0,
      // 暂存没重复的文件
      nDuplicateFiles: [],
      nDuplicateKeywords: [],
      nServerFullFileNames: [],
      // 存放已经上传的文件大小
      resultValue: [],
      percentBox: [],
      progressPercent: "",
      // 已经上传的文件大小的块
      resultChunks: ""
    };
  },
  watch: {
    uploadArr: {
      handler(newValue) {
        if (newValue.length > 0) {
          this.autoUpload = newValue.autoUpload;
          this.fileList = newValue;
          this.DuplicateFileHandle();
        }
      },
      deep: true
    }
  },
  computed: {
    ...mapGetters("doc", ["ProjectKeyWord"])
  },
  methods: {
    reInTray(e) {
      this.InTrayData.switch = e;
    },
    // 文件暂存
    handleChange(file, fileList) {
      this.fileList = fileList;
      // if (this.autoUpload) {
      clearTimeout(this.TimeId);
      this.TimeId = setTimeout(() => {
        this.DuplicateFileHandle();
      }, 500);
      // }
    },
    // 第一步 - 判断是否有重复文件
    async DuplicateFileHandle() {
      let vm = this,
        { fileList } = vm,
        len = fileList.length,
        confirmUpgrade = "",
        st = false;
      vm.fileNum = len;
      for (let i = 0; i < len; i++) {
        const res = await UserApi3.CreateDocByFileName(
          this.ProjectKeyWord,
          fileList[i].name,
          confirmUpgrade
        );
        if (res.success) {
          // 有重复
          if (res.msg === "ConfirmUpgrade") {
            st = true;
            if (fileList[i].raw) {
              vm.DuplicateFiles.push(fileList[i].raw);
            } else {
              vm.DuplicateFiles.push(fileList[i]);
            }
          } else {
            // 没有重复, 会新建文件
            vm.nDuplicateKeywords.push(res.data[0].Keyword);
            if (fileList[i].raw) {
              vm.nDuplicateFiles.push(fileList[i].raw);
            } else {
              vm.nDuplicateFiles.push(fileList[i]);
            }
          }
        } else {
          this.$message({
            type: "error",
            message: res.msg
          });
        }
      }
      if (st) vm.upgradeDialogVisible = true;
      if (vm.nDuplicateFiles.length > 0) vm.beforeAvatarUpload();
    },
    // 第二步 - 没有重复文件
    async beforeAvatarUpload() {
      let vm = this,
        { nDuplicateFiles } = vm,
        { nDuplicateKeywords } = vm,
        arr = [],
        len = nDuplicateFiles.length;
      this.$store.commit("doc/LIST", nDuplicateFiles);
      for (let i = 0; i < len; i++) {
        let fileSize = nDuplicateFiles[i].size,
          ModifyDate = TimeChange(nDuplicateFiles[i].lastModifiedDate),
          ServerFileName = nDuplicateFiles[i].name,
          ObjectKeyword = nDuplicateKeywords[i],
          CreateDate = "";
        const res = await UserApi2.beforeUploadFile(
          ObjectKeyword,
          ServerFileName,
          CreateDate,
          ModifyDate,
          fileSize
        );
        arr.push({
          fileName: nDuplicateFiles[i].name,
          size: nDuplicateFiles[i].size
        });
        if (res.success) {
          // 已经上传的文件大小
          vm.resultValue.push(res.data[0].resultValue);
          vm.nServerFullFileNames.push(res.data[0].ServerFullFileName);
        } else {
          vm.$message({ message: res.msg, type: "error" });
        }
      }
      this.fileData = arr;
      vm.httpRequest();
    },
    // 第三步 - 上传文件
    async httpRequest() {
      let vm = this,
        { nServerFullFileNames } = vm,
        { nDuplicateFiles, multiUploadSize, eachSize } = vm,
        len = nServerFullFileNames.length;
      vm.progressArr = [];
      vm.percent = 0;
      let sTime = new Date().getTime(); // 获取上传开始时间
      for (let i = 0; i < len; i++) {
        // 打开进度条弹窗
        vm.progressVisible = true;
        // 计算总块数
        let chunks = Math.ceil(nDuplicateFiles[i].size / multiUploadSize),
          fileChunks = [];
        // 大于限制的上传大小就进行分块
        if (nDuplicateFiles[i].size > multiUploadSize) {
          // 切块
          fileChunks = await this.splitFile(
            nDuplicateFiles[i],
            eachSize,
            chunks
          );
          for (let j = 0; j < fileChunks.length; j++) {
            let data = new FormData();
            data.append("sid", this.$store.getters.sid);
            data.append("C", "AVEVA.CDMS.WebApi.FileController");
            data.append("A", "UploadFile");
            data.append("ServerFullFileName", nServerFullFileNames[i]);
            data.append("chunks", chunks);
            vm.resultChunks = 0;
            vm.uploadSpeed = 0;
            // 计算已经上传的大小有多少块
            vm.resultValue.forEach((item, index) => {
              if (item > 0 && i === index) {
                vm.resultChunks = Math.ceil(item / multiUploadSize);
              }
            });
            // 断点续传
            if (vm.resultChunks > 0) {
              data.append("chunk", j + vm.resultChunks);
              data.append("file", fileChunks[j + vm.resultChunks]);
              //计算分块上传进度条
              vm.progressPercent = Math.ceil(
                ((j + 1 + vm.resultChunks) / chunks) * 100
              );
            } else {
              data.append("chunk", j);
              data.append("file", fileChunks[j]);
              //计算分块上传进度条
              vm.progressPercent = Math.ceil(((j + 1) / chunks) * 100);
            }
            vm.progressArr.push(vm.progressPercent);
            let arr = await this.splitProgress(vm.progressArr, 100);
            vm.percentBox = [];
            // 计算上传速度
            let eTime = new Date().getTime(); // 结束时间
            let time = eTime - sTime; // 获取时间差
            let speed = fileChunks[j].size / 1024 / (time / 1000); //当前上传的文件大小除以时间差 kb/s
            if (speed / 1024 > 1) {
              vm.uploadSpeed = (speed / 1024).toFixed(2);
              vm.unit = "M/s";
            } else {
              vm.uploadSpeed = speed.toFixed(2);
              vm.unit = "kb/s";
            }
            sTime = eTime;
            const res = await UserApi2.uploadFile(data);
            if (res.success) {
              // 把进度数据放入
              vm.fileData.forEach((item, index) => {
                arr.forEach((v, idx) => {
                  if (index === idx) {
                    v.forEach(j => {
                      vm.$set(item, "progressBar", j);
                    });
                  }
                });
              });
              // 总进度信息
              let num = 0;
              arr.forEach(item => {
                item.forEach(i => {
                  if (i !== 100) {
                    vm.percent = ((i + num) / len).toFixed(0);
                  } else {
                    vm.percentBox.push(i);
                    num += i;
                    vm.percent = (eval(vm.percentBox.join("+")) / len).toFixed(
                      0
                    );
                  }
                });
              });
              // 续传完成返回FINISH 则终止此次循环
              if (res.msg == "FINISH") {
                break;
              }
            } else {
              vm.$refs.upload.clearFiles();
              if (res.msg !== "") {
                vm.$message({ message: res.msg, type: "error" });
              }
            }
          }
        } else {
          let data = new FormData();
          data.append("sid", this.$store.getters.sid);
          data.append("C", "AVEVA.CDMS.WebApi.FileController");
          data.append("A", "UploadFile");
          data.append("ServerFullFileName", nServerFullFileNames[i]);
          data.append("Files", nDuplicateFiles[i]);
          const res = await UserApi2.uploadFile(data, {
            // 获取每个上传文件的进度条信息
            onUploadProgress(progress) {
              vm.progressPercent = Math.round(
                (progress.loaded / progress.total) * 100
              );
              vm.progressArr.push(vm.progressPercent);
              // 计算上传速度
              let eTime = new Date().getTime(); // 结束时间
              let time = eTime - sTime; // 获取时间差
              let speed = progress.loaded / 1024 / (time / 1000); //当前上传的文件大小除以时间差 kb/s
              if (speed / 1024 > 1) {
                vm.uploadSpeed = (speed / 1024).toFixed(2);
                vm.unit = "M/s";
              } else {
                vm.uploadSpeed = speed.toFixed(2);
                vm.unit = "kb/s";
              }
              sTime = eTime;
            }
          });
          let arr = await this.splitProgress(vm.progressArr, 100);
          vm.percentBox = [];
          // 总进度信息
          arr.forEach(item => {
            item.forEach(i => {
              if (i !== 100) {
                vm.percent = i / len;
              } else {
                vm.percentBox.push(i);
                vm.percent = (eval(vm.percentBox.join("+")) / len).toFixed(0);
              }
            });
          });
          if (res.success) {
            // 把进度数据放入
            vm.fileData.forEach((item, index) => {
              arr.forEach((v, idx) => {
                if (index === idx) {
                  v.forEach(j => {
                    vm.$set(item, "progressBar", j);
                  });
                }
              });
            });
          } else {
            // new Promise(res);
            vm.$refs.upload.clearFiles();
            if (res.msg !== "") {
              vm.$message({ message: res.msg, type: "error" });
            } else if (res.msg == "REUPLOAD") {
              Promise(res);
            }
          }
        }
      }
      vm.onSuccess();
    },
    // 第四步 - 上传成功
    async onSuccess() {
      let vm = this,
        { nServerFullFileNames } = vm,
        { nDuplicateKeywords } = vm,
        len = nServerFullFileNames.length;
      for (let i = 0; i < len; i++) {
        const res = await UserApi2.afterUploadFile(
          nDuplicateKeywords[i],
          nServerFullFileNames[i]
        );
        if (res.data[0].state === "uploadSuccess") {
          const res2 = await UserApi3.afterCreateNewDocEvent(
            nDuplicateKeywords[i]
          );
          if (res2.success) {
            this.progressVisible = false;
            vm.$message({ message: "上传成功", type: "success" });
            if (res2.msg == "GetDocument") {
              this.InTrayData.switch = true;
              this.InTrayData.docKeyWord = nDuplicateKeywords[i];
            }
          }
        }
      }
      vm.fileList = [];
      vm.nDuplicateFiles = [];
      vm.nDuplicateKeywords = [];
      vm.nServerFullFileNames = [];
      vm.$refs.upload.clearFiles();
      const { ProjectKeyWord } = vm,
        filterJson = "",
        page = 1,
        limit = 50;
      vm.$store.dispatch("doc/getDocList", {
        ProjectKeyWord,
        filterJson,
        page,
        limit
      });
    },
    // 弹窗按钮 - 全部
    confirmAllHandle(e) {
      this.isAll = e;
    },
    // 弹窗按钮 - 单个
    // 按钮返回值
    async confirmHandle(e) {
      let vm = this,
        { DuplicateFiles } = vm,
        len = DuplicateFiles.length,
        { i } = vm;
      vm.confirmUpgrade = e;
      for (i; i < len; i++) {
        vm.upgradeDialogVisible = false;
        vm.i = i + 1;
        if (vm.isAll) {
          const res = await UserApi3.CreateDocByFileName(
            vm.ProjectKeyWord,
            vm.DuplicateFiles[i].name,
            vm.confirmUpgrade
          );
          vm.DuplicateKeywords.push({
            Keyword: res.data[0].Keyword,
            name: res.data[0].O_filename
          });
          if (vm.i === len) vm.beforeAvatarUpload2();
        } else {
          const res = await UserApi3.CreateDocByFileName(
            vm.ProjectKeyWord,
            vm.DuplicateFiles[i].name,
            vm.confirmUpgrade
          );
          vm.DuplicateKeywords.push({
            Keyword: res.data[0].Keyword,
            name: res.data[0].O_filename
          });
          if (vm.i !== len) {
            setTimeout(() => {
              vm.upgradeDialogVisible = true;
            }, 500);
          } else {
            setTimeout(() => {
              vm.beforeAvatarUpload2();
            }, 500);
          }
          return;
        }
      }
    },
    // 第二步 - 有重复文件
    async beforeAvatarUpload2() {
      let vm = this,
        { DuplicateFiles } = vm,
        { DuplicateKeywords } = vm,
        arr = [],
        len = DuplicateFiles.length;
      this.$store.commit("doc/LIST", DuplicateFiles);
      for (let i = 0; i < len; i++) {
        let fileSize = DuplicateFiles[i].size,
          ModifyDate = TimeChange(DuplicateFiles[i].lastModifiedDate),
          ServerFileName = DuplicateKeywords[i].name,
          ObjectKeyword = DuplicateKeywords[i].Keyword,
          CreateDate = "";
        const res = await UserApi2.beforeUploadFile(
          ObjectKeyword,
          ServerFileName,
          CreateDate,
          ModifyDate,
          fileSize
        );
        arr.push({
          fileName: DuplicateFiles[i].name,
          size: DuplicateFiles[i].size
        });
        if (res.success) {
          vm.resultValue.push(res.data[0].resultValue);
          vm.ServerFullFileNames.push(res.data[0].ServerFullFileName);
        } else {
          vm.$message({ message: res.msg, type: "error" });
        }
      }
      this.fileData = arr;
      vm.httpRequest2();
    },
    // 第三步重复 - 上传文件
    async httpRequest2() {
      let vm = this,
        { ServerFullFileNames, multiUploadSize, eachSize } = vm,
        { DuplicateFiles } = vm,
        len = ServerFullFileNames.length;
      vm.progressArr = [];
      vm.percent = 0;
      vm.uploadSpeed = 0;
      for (let i = 0; i < len; i++) {
        // 打开进度条弹窗
        vm.progressVisible = true;
        // 计算总块数
        let chunks = "",
          fileChunks = [];
        vm.resultValue.forEach(item => {
          if (item > 0) {
            chunks = Math.ceil(
              (DuplicateFiles[i].size - item) / multiUploadSize
            );
          } else {
            chunks = Math.ceil(DuplicateFiles[i].size / multiUploadSize);
          }
        });
        let sTime = new Date().getTime(); // 获取上传开始时间
        // 大于限制的上传大小就进行分块
        if (DuplicateFiles[i].size > multiUploadSize) {
          // 切块
          fileChunks = await this.splitFile(
            DuplicateFiles[i],
            eachSize,
            chunks
          );
          for (let j = 0; j < fileChunks.length; j++) {
            let data = new FormData();
            data.append("sid", this.$store.getters.sid);
            data.append("C", "AVEVA.CDMS.WebApi.FileController");
            data.append("A", "UploadFile");
            data.append("ServerFullFileName", ServerFullFileNames[i]);
            data.append("file", fileChunks[j]);
            data.append("chunks", chunks);
            data.append("chunk", j);
            //计算分块上传进度条
            vm.progressPercent = Math.ceil(((j + 1) / chunks) * 100);
            vm.progressArr.push(vm.progressPercent);
            let arr = await this.splitProgress(vm.progressArr, 100);
            vm.percentBox = [];
            // 计算上传速度
            let eTime = new Date().getTime(); // 结束时间
            let time = eTime - sTime; // 获取时间差
            let speed = fileChunks[j].size / 1024 / (time / 1000); //当前上传的文件大小除以时间差 kb/s
            if (speed / 1024 > 1) {
              vm.uploadSpeed = (speed / 1024).toFixed(2);
              vm.unit = "M/s";
            } else {
              vm.uploadSpeed = speed.toFixed(2);
              vm.unit = "kb/s";
            }
            sTime = eTime;
            const res = await UserApi2.uploadFile(data);
            if (res.success) {
              // 把进度数据放入
              vm.fileData.forEach((item, index) => {
                arr.forEach((v, idx) => {
                  if (index === idx) {
                    v.forEach(j => {
                      vm.$set(item, "progressBar", j);
                    });
                  }
                });
              });
              // 总进度信息
              let num = 0;
              arr.forEach(item => {
                item.forEach(i => {
                  if (i !== 100) {
                    vm.percent = ((i + num) / len).toFixed(0);
                  } else {
                    vm.percentBox.push(i);
                    num += i;
                    vm.percent = (eval(vm.percentBox.join("+")) / len).toFixed(
                      0
                    );
                  }
                });
              });
            } else {
              vm.$refs.upload.clearFiles();
              if (res.msg !== "") {
                vm.$message({ message: res.msg, type: "error" });
              }
            }
          }
        } else {
          let data = new FormData();
          data.append("sid", this.$store.getters.sid);
          data.append("C", "AVEVA.CDMS.WebApi.FileController");
          data.append("A", "UploadFile");
          data.append("ServerFullFileName", ServerFullFileNames[i]);
          data.append("Files", DuplicateFiles[i]);
          const res = await UserApi2.uploadFile(data, {
            // 获取每个上传文件的进度条信息
            onUploadProgress(progress) {
              vm.progressPercent = Math.round(
                (progress.loaded / progress.total) * 100
              );
              vm.progressArr.push(vm.progressPercent);
              // 计算上传速度
              let eTime = new Date().getTime(); // 结束时间
              let time = eTime - sTime; // 获取时间差
              let speed = progress.loaded / 1024 / (time / 1000); //当前上传的文件大小除以时间差 kb/s
              if (speed / 1024 > 1) {
                vm.uploadSpeed = (speed / 1024).toFixed(2);
                vm.unit = "M/s";
              } else {
                vm.uploadSpeed = speed.toFixed(2);
                vm.unit = "kb/s";
              }
              sTime = eTime;
            }
          });
          let arr = await this.splitProgress(vm.progressArr, 100);
          vm.percentBox = [];
          // 总进度信息
          arr.forEach(item => {
            item.forEach(i => {
              if (i !== 100) {
                vm.percent = i / len;
              } else {
                vm.percentBox.push(i);
                vm.percent = (eval(vm.percentBox.join("+")) / len).toFixed(0);
                console.log(vm.percent);
              }
            });
          });
          if (res.success) {
            // 把进度数据放入
            vm.fileData.forEach((item, index) => {
              arr.forEach((v, idx) => {
                if (index === idx) {
                  v.forEach(j => {
                    vm.$set(item, "progressBar", j);
                  });
                }
              });
            });
          } else {
            vm.$refs.upload.clearFiles();
            if (res.msg !== "") {
              vm.$message({ message: res.msg, type: "error" });
            }
          }
        }
      }
      vm.onSuccess2();
    },
    // 第四步重复 - 上传成功
    async onSuccess2() {
      let vm = this,
        { ServerFullFileNames } = vm,
        { DuplicateKeywords } = vm,
        len = ServerFullFileNames.length;
      for (let i = 0; i < len; i++) {
        const res = await UserApi2.afterUploadFile(
          DuplicateKeywords[i].Keyword,
          ServerFullFileNames[i]
        );
        if (res.data[0].state === "uploadSuccess") {
          const res2 = await UserApi3.afterCreateNewDocEvent(
            DuplicateKeywords[i].Keyword
          );
          if (res2.success) {
            this.progressVisible = false;
            vm.$message({ message: "上传成功", type: "success" });
            if (res2.msg == "GetDocument") {
              this.InTrayData.switch = true;
              this.InTrayData.docKeyWord = DuplicateKeywords[i];
            }
          }
        }
      }
      this.$emit("reData");
      vm.fileList = [];
      vm.DuplicateFiles = [];
      vm.DuplicateKeywords = [];
      vm.ServerFullFileNames = [];
      vm.i = 0;
      vm.isAll = "";
      vm.$refs.upload.clearFiles();
      const filterJson = "",
        page = 1,
        limit = 50;
      let { ProjectKeyWord } = vm;
      vm.$store.dispatch("doc/getDocList", {
        ProjectKeyWord,
        filterJson,
        page,
        limit
      });
    },
    // 上传文件失败
    onError() {
      this.$message.error("上传失败");
    },
    // 关闭升级覆盖弹窗
    handleClose() {
      let vm = this;
      vm.fileList = [];
      vm.DuplicateFiles = [];
      vm.DuplicateKeywords = [];
      vm.ServerFullFileNames = [];
      vm.i = 0;
      vm.isAll = "";
      vm.$refs.upload.clearFiles();
      this.progressVisible = false;
      this.upgradeDialogVisible = false;
    },
    // 文件分块
    splitFile(file, eachSize, chunks) {
      return new Promise((resolve, reject) => {
        try {
          setTimeout(() => {
            const fileChunk = [];
            for (let chunk = 0; chunks > 0; chunks--) {
              fileChunk.push(file.slice(chunk, chunk + eachSize));
              chunk += eachSize;
            }
            resolve(fileChunk);
          }, 0);
        } catch (e) {
          reject(new Error("文件切块发生错误"));
        }
      });
    },
    // 拆分进度条数组
    splitProgress(arr, n) {
      let start = 0;
      let res = [];
      while (arr.indexOf(n, start) > -1) {
        let end = arr.indexOf(n, start) + 1;
        res.push(arr.slice(start, end));
        start = end;
      }
      if (start < arr.length) {
        res.push(arr.slice(start));
      }
      return res;
    },
    cancelUpload() {}
  },
  filters: {
    filterSize(val) {
      if (val == 0) return "0 B";
      const k = 1024;
      const sizes = ["B", "KB", "MB", "GB"],
        i = Math.floor(Math.log(val) / Math.log(k));
      return (val / Math.pow(k, i)).toPrecision(3) + " " + sizes[i];
    }
  }
};
</script>
<style lang="scss">
.uploadProgress {
  text-align-last: left;
  .delUpload {
    font-size: 20px;
    cursor: pointer;
    &:hover {
      color: #409eff;
    }
  }
  .uploadDetails {
    text-align: left;
    font-weight: bold;
  }
}
</style>